body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgba(45, 108, 230, 0.04);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}
.over-flow-hide{
  overflow: hidden;
  
}
.center{
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.topbar{
  display:flex;
  height:1.357vw;
  width: 100%;
  background: white;
  
}
.topbar-left{
background: #1B74FA;
height:1.357vw;
width:20.31%
}


/* badge bannerrrrrrrrrrrrrrrr */
.badge-banner{
  margin-top:6rem;
  width:100%;
  height: 70vh;
  position: relative;
  border-radius: 16px;
}
.badge-banner .badge-ellipse1{
  position: absolute;
  left: 0;
  top: 0;
  width: 32%;
  
  overflow: hidden;
  border-radius: 16px;
}
.badge-banner .badge-ellipse2{
  position: absolute;
  right: 0;
  bottom: 0;
  width: 39%;
  border-radius: 16px;

}
.badge-img{
  width: 47%;
}

.badge-banner-content{ 
  margin-top: 2.5rem;
  width: 100%;
  height: 21.1vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.badge-banner-content span{
  font-family: 'Open Sans', sans-serif;

font-weight: bold;
font-size: 2.813rem;
color: #2D2F32;

}
.badge-banner-content p{
  font-family: 'Open Sans', sans-serif;
  font-weight: normal;
  margin: 0px;
  font-size: 2.6rem;
  color: #2D2F32;
}




/* badge companies */
.badge-companies{
  margin-top:1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.badge-companies span{
  font-family: Open Sans;
font-style: normal;
font-weight: bold;
font-size: 32px;
line-height: 57px;
text-align: center;
}
.badge-companies .comp-grid{
  width: 80%;
  
  display: flex;
  justify-content: space-between;
  margin-top:60px;
}
.badge-companies .comp-grid-item{
  width: 240px;
  
  height: 190px;
  background: #FFFFFF;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
  border-radius: 16px; 
}
.comp-grid-item .comp-img{
  width: 190px;
}
.comp-grid-item .small-img{
  width: 240px;
}
.comp-grid-item .big-img{
  width: 130px;
}

.videoplay{
  margin-top:7rem;
}

.videoplay img{
  width: 80vw;
  
}

.btn{
  width:400px;
  height:80px;
  background: #1B74FA;
  border-radius: 11px;
  margin-top:4rem;
  font-family: Roboto;
 text-transform:capitalize;
font-size: 25px;


}
